import { Injectable } from '@angular/core';

import { Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';

@Injectable()
export class UserDataProvider {
  _favorites: string[] = [];
  HAS_LOGGED_IN = 'hasLoggedIn';
  HAS_SEEN_TUTORIAL = 'hasSeenTutorial';
  public isLogin = false;
  public cartItemCount:any = '';
  public cartPrice = 0;
  public messageCount:any = '';
  public cartList: any = [];
  public messageList: any = [];
  constructor(
    public events: Events,
    public storage: Storage
  ) { }

  hasFavorite(sessionName: string): boolean {
    return (this._favorites.indexOf(sessionName) > -1);
  };

  addFavorite(sessionName: string): void {
    this._favorites.push(sessionName);
  };

  removeFavorite(sessionName: string): void {
    let index = this._favorites.indexOf(sessionName);
    if (index > -1) {
      this._favorites.splice(index, 1);
    }
  };

  login(username: string): void {
    this.storage.set(this.HAS_LOGGED_IN, true);
    this.setUsername(username);
    this.events.publish('user:login');
    this.isLogin = true;
  };

  signup(username: string): void {
    this.storage.set(this.HAS_LOGGED_IN, true);
    this.setUsername(username);
    this.events.publish('user:signup');
    this.isLogin = true;
  };

  logout(): void {
    this.storage.remove(this.HAS_LOGGED_IN);
    this.storage.remove('username');
    this.events.publish('user:logout');
    this.isLogin = false;
  };

  setUsername(username: string): void {
    this.storage.set('username', username);
  };

  getUsername(): Promise<string> {
    return this.storage.get('username').then((value) => {
      return value;
    });
  };

  hasLoggedIn(): Promise<boolean> {
    return this.storage.get(this.HAS_LOGGED_IN).then((value) => {
      this.isLogin = (value === true);
      return this.isLogin;
    });
  };

  checkHasSeenTutorial(): Promise<string> {
    return this.storage.get(this.HAS_SEEN_TUTORIAL).then((value) => {
      return value;
    });
  };

  addCartItem(item: any) {
    const copy = Object.assign({ no: new Date(Date.now()).getTime() }, item);
    this.cartList.push(copy);
    this.refreshCart();
    console.log('add Cart');
  };

  removeCartItem(item: any) {
    console.log('remove Cart');
    console.log(item);
    if (!item.no) {
      return;
    }
    let index = 0;
    let indexValue = -1;
    this.cartList.forEach(function (element) {
      if (element.no == item.no) {
        indexValue = index;
      }
      index++;
    });
    if (indexValue < 0) {
      return;
    }
    console.log(indexValue);
    this.cartList.splice(indexValue, 1);
    this.refreshCart();
  };

  refreshCart() {
    let price = 0;
    const newlist = [];
    this.cartList.forEach(function (element) {
      price += element.price;
      newlist.push(element);
    });
    this.cartList = newlist;
    this.cartItemCount = this.cartList.length;
    if(this.cartItemCount == ''){
      this.cartItemCount = '';
    }
    this.cartPrice = price;
  }

  saveOrder(){
    const now = Date.now();

    this.messageList.push({
      no: new Date(now).getTime(),
      creativeDate : now,
      price: this.cartPrice,
      step: 0,
      state: '待付款'
    });
    this.messageCount = this.messageList.length;
    this.cartList = [];
    this.cartPrice = 0;
    this.cartItemCount = '';
  }
}



