import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { SplashScreen } from '@ionic-native/splash-screen';
import { IonicStorageModule } from '@ionic/storage';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TutorialPage } from '../pages/tutorial/tutorial';
import { AboutPage } from '../pages/about/about';
import { CartPage } from '../pages/cart/cart';
import { NotificationsPage } from '../pages/notifications/notifications';
import { TabsPage } from '../pages/tabs/tabs';
import { UserDataProvider } from '../providers/user-data';
import { SignupPage } from '../pages/signup/signup';
import { LoginPage } from '../pages/login/login';
import { FilterPage } from '../pages/filter/filter';
import { DetailPage } from '../pages/detail/detail';
import { PayPage } from '../pages/pay/pay';

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    SignupPage,
    TutorialPage,
    TabsPage,
    HomePage,
    AboutPage,
    CartPage,
    NotificationsPage,
    FilterPage,
    DetailPage,
    PayPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, {
      links: [
        { component: LoginPage, name: 'LoginPage', segment: 'login' },
        { component: SignupPage, name: 'SignupPage', segment: 'signup' },
        { component: TutorialPage, name: 'Tutorial', segment: 'tutorial' },
        { component: HomePage, name: 'Home', segment: 'home' },
        { component: CartPage, name: 'Cart', segment: 'cart' },
        { component: NotificationsPage, name: 'Notifications', segment: 'notifications' },
        { component: AboutPage, name: 'About', segment: 'about' },
        { component: FilterPage, name: 'Filter', segment: 'filter' },
        { component: DetailPage, name: 'Detail', segment: 'detail' },
        { component: PayPage, name: 'Pay', segment: 'pay' }
      ]
    }),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    SignupPage,
    TutorialPage,
    TabsPage,
    HomePage,
    CartPage,
    NotificationsPage,
    AboutPage,
    FilterPage,
    DetailPage,
    PayPage
  ],
  providers: [
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    SplashScreen,
    UserDataProvider
  ]
})
export class AppModule { }



